.header {
    display: flex;
    height: 4rem;
    padding: 0.8rem 1rem;
    background: black;
    color: white;
}

.logo {
    display: flex;
    align-items: center;
    margin-right: 1em;
}

.img {
    margin-right: 0.5em;
}

.input {
    flex-basis: 100%;
    font-size: 1.2rem;
    outline: 0;
}

.button {
    background-color: darkgray;
    outline: 0;
}

.buttonImg {
    height: 100%;
    padding: 0.5em 0.2em;
    cursor: pointer;
}
